CpB;KACJ;IACD;QACI;;YAEI,YAAY;YACZ,iBAAiB;SACpB;KACJ;IACD;QACI;;YAEI,aAAa;YACb,iBAAiB;SACpB;KACJ;IACD;QACI;;YAEI,YAAY;YACZ,WAAW;SACd;KACJ;IACD;QACI;;YAEI,mBAAmB;SACtB;KACJ;IACD;QACI;;YAEI,qBAAqB;SACxB;KACJ;IACD;QACI;;YAEI,sBAAsB;SACzB;KACJ;IACD;QACI;;YAEI,oBAAoB;SACvB;KACJ;CACJ","file":"player.css","sourcesContent":[".hide {\n    display: none;\n}\n\n.disable {\n    pointer-events: none;\n}\n\n.fjfigure {\n    height: auto;\n    margin: 0 0 0;\n    background-color: rgb(0, 0, 0);\n}\n\n.fjPlayer {\n    background-color: rgba(0, 0, 0, 0.5);\n    color: rgb(255, 255, 255);\n    text-align: center;\n    position: relative;\n    margin: 0px auto 0px auto;\n    display: inline-block;\n    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n}\n\n.fjplayerExpand {\n    width: 100%;\n}\n\n.divofvideo {\n    -webkit-background-size: cover;\n    -moz-background-size: cover;\n    -o-background-size: cover;\n    background-size: cover;\n}\n\n\n/* ---------------------- Thumbs style --------------------- */\n\n.thumbsBlockDiv {\n    position: absolute;\n    display: table;\n    visibility: hidden;\n    border-radius: 2px;\n    z-index: 2147483647;\n    /*background: rgba(0, 0, 0, 0.5);*/\n    background: rgba(10, 10, 10, 0.8);\n    font-size: 12px;\n    border-radius: 5px;\n}\n\n.thumbsBlock {\n    display: table-cell;\n    border-radius: 2px;\n    vertical-align: top;\n    display: inline-block;\n}\n\n\n/*______________________________ CONTROLS __________________________*/\n\n.divcontrols {\n    margin: 0px auto 0px auto;\n    width: 100%;\n    height: 25px;\n    position: absolute;\n    visibility: visible;\n    bottom: 0px;\n    left: 0px;\n}\n\n.divcontrolsProgress {\n    margin: 0px auto 0px auto;\n    width: 100%;\n    height: 8px;\n    position: absolute;\n    visibility: visible;\n    bottom: 25px;\n    left: 0px;\n}\n\n.divconeontrolLeft {\n    float: left;\n}\n\n.divconeontrolRight {\n    float: right;\n}\n\n.divconeontrolIcon {\n    width: 30px;\n}\n\n.divconeontrolVBar {\n    width: 60px;\n}\n\n.divonecontrol {\n    height: 25px;\n    padding-top: 2px;\n    padding-right: 1px;\n}\n\n.divTitle,\n.divcontrols,\n.control,\n.divonecontrol {\n    display: inline-block;\n    vertical-align: top;\n}\n\n\n/* this rule is for IE7 and below only\nput it in a Conditional comment without the !ie7 hack if you wish! */\n\n.divcontrols,\n.control,\n.divonecontrol {\n    display: inline !ie7;\n    margin: 0 2px !ie7;\n}\n\n.controlText {\n    padding-top: 2px;\n    font-size: 0.75em;\n    white-space: nowrap;\n}\n\n.divInfo {\n    z-index: 2147483647;\n    border-radius: 5px;\n    top: 10%;\n    width: 80%;\n    position: absolute;\n    left: 10%;\n}\n\n.divBigPlayBtn {\n    font-size: 3.2em;\n    z-index: 2147483647;\n    border-radius: 5px;\n    top: 35%;\n    position: absolute;\n    left: 45%;\n    background-color: rgba(0, 0, 0, 0.5);\n    border-radius: 2%;\n    padding: 2%;\n}\n\n.divBigPlayBtn:hover {\n    background: #333;\n}\n\n.divIconBtn {\n    position: relative;\n    text-overflow: ellipsis;\n    overflow: hidden;\n    white-space: nowrap;\n    display: inline-block;\n    cursor: pointer;\n    opacity: 0.8;\n}\n\n.divIconBtn:hover {\n    opacity: 1.0;\n    background: #333;\n}\n\n.divTspanitleSeparator {\n    position: relative;\n    visibility: visible;\n    text-overflow: ellipsis;\n    overflow: hidden;\n    margin: 0px;\n    white-space: nowrap;\n    display: inline-block;\n    max-width: 766px;\n    font-size: 12px;\n    padding: 10px;\n}\n\n\n/*_________________________  Progress  ________________________*/\n\n.progressbar {\n    position: relative;\n    visibility: visible;\n    width: 100%;\n    display: inline-block;\n    cursor: pointer;\n    height: 10px;\n    padding-top: 2px;\n    padding-right: 1px;\n    bottom: 10px;\n}\n\n.volumebar {\n    margin-top: 2%;\n    padding-left: 1%;\n    padding-right: 3%;\n    border-left: none;\n    border-right: none;\n}\n\n\n/*_________________________ RANGE ________________________*/\n\ninput[type=\"range\"] {\n    -webkit-appearance: none;\n    -moz-apperance: none;\n    cursor: pointer;\n    width: 98%;\n    left: 1%;\n    border-radius: 6px;\n    height: 8px;\n    background-image: -webkit-gradient( linear, left top, right top, color-stop(0, #FF0000), color-stop(0, #8F9B9E));\n}\n\ninput[type='range']::-webkit-slider-thumb {\n    -webkit-appearance: none !important;\n    background-color: #FF0000;\n    border: 1px solid rgba(10, 10, 10, 0.8);\n    height: 14px;\n    width: 14px;\n    margin-left: -1.5px;\n    border-radius: 100%;\n    box-shadow: 2px 2px rgba(0, 0, 0, 0.5);\n}\n\ninput[type='range']::-moz-slider-thumb {\n    -webkit-appearance: none !important;\n    background-color: #FF0000;\n    border: 1px solid rgba(10, 10, 10, 0.8);\n    height: 14px;\n    width: 14px;\n    margin-left: -1.5px;\n    border-radius: 100%;\n    box-shadow: 2px 2px rgba(0, 0, 0, 0.5);\n}\n\ninput[type=range]:focus {\n    outline: none;\n}\n\n\n/* -------------------  CC SUBTITLES --------------------- */\n\n.subtitles-menu-item-actif {\n    cursor: pointer;\n    text-align: left;\n    border-radius: 2px;\n    opacity: 1;\n}\n\n.subtitles-menu-item {\n    cursor: pointer;\n    text-align: left;\n}\n\n.fj-hide {\n    visibility: hidden;\n    opacity: 0;\n    transition: visibility 200ms, opacity 200ms;\n}\n\n.fj-show {\n    visibility: visible;\n}\n\n\n/* animation */\n\n.m-fadeOut {\n    visibility: hidden;\n    opacity: 0;\n    transition: visibility 200ms, opacity 200ms;\n}\n\n.m-fadeIn {\n    visibility: visible;\n    opacity: 1;\n}\n\n\n/* ---------------------- Menu style --------------------- */\n\n.settingMenuDiv {\n    position: absolute;\n    background: rgba(10, 10, 10, 0.8);\n    float: left;\n    font-size: 12px;\n    /* visibility: hidden;*/\n    padding: 5px;\n    border-radius: 5px;\n    z-index: 2147483647;\n}\n\nul {\n    padding: 2px;\n    margin: 2px;\n    list-style-type: none;\n}\n\nul li {}\n\n.settingMenuSubMenuRight {\n    float: left;\n    width: 100%;\n    border-left: 1px solid #2A2A2A;\n    padding-left: 5px;\n    padding-right: 5px;\n}\n\n\n/* CUES and subtitles */\n\n ::cue {\n    color: #ccc;\n}\n\n ::cue(v[voice='Test']) {\n    color: #fff;\n    background: #0095dd;\n}\n\n\n/* OVERLAYS */\n\n.fjOvcontainer {\n    width: 100%;\n    height: 100%;\n    margin: 0 auto;\n    padding: 0px;\n    /*position: relative;*/\n    position: absolute;\n}\n\n.over-HL {\n    position: absolute;\n    background: rgba(0, 0, 0, 0.5);\n    float: left;\n    font-size: 12px;\n    padding: 5px;\n    border-radius: 5px;\n    z-index: 2147483647;\n    /****/\n    top: 20%;\n    left: 20%;\n    height: auto;\n}\n\n.over-DL {\n    position: absolute;\n    background: rgba(0, 0, 0, 0.5);\n    float: center;\n    font-size: 12px;\n    border-radius: 5px;\n    z-index: 2147483647;\n    bottom: 2%;\n    width: 96%;\n    left: 2%;\n    height: auto;\n}\n\n\n/*__________________________________________ FJCONTROL ____________________________*/\n\n.fjcontrols-panel {\n    z-index: 2147483647;\n    position: absolute;\n    border-radius: 5px;\n    bottom: 3%;\n    opacity: 1;\n    background: rgba(10, 10, 10, 0.8);\n    width: 90%;\n    margin-left: 5%;\n    display: relative;\n    float: left;\n}\n\n.fjcontrols-panel-controls {\n    width: 100%;\n    position: relative;\n    margin-bottom: 2px;\n}\n\n.fjcontrols-control {\n    margin-bottom: 2px;\n    position: relative;\n    float: left;\n    height: 100%;\n    /*border-right: 1px solid #2A2A2A;*/\n}\n\n.fjcontrols-control-right {\n    float: right;\n    /*  border-left: 1px solid #2A2A2A; */\n    border-right: none;\n}\n\n.fjcontrols-control-text {\n    font-size: 0.9em;\n    cursor: auto;\n    padding-left: 1%;\n    padding-right: 1%;\n    border-left: none;\n    border-right: none;\n}\n\n.fjcontrols-control-text-small {\n    font-size: 1em;\n    line-height: 2;\n}\n\n.hprogressbar {\n    width: 99%;\n    left: 0.5%;\n    text-align: center;\n    display: inline-block;\n}\n\n.vprogressbar {\n    display: none;\n    width: 15%;\n    border-radius: 5px;\n    padding: 1px;\n    margin-left: 80%;\n    margin-right: 5%;\n    margin-top: 1%;\n    margin-bottom: 1%;\n    cursor: auto;\n}\n\n.vprogressbar>div {\n    width: 80%;\n    height: 10px;\n    border-radius: 5px;\n}\n\nhr {\n    margin-top: 5px;\n    margin-bottom: -1px;\n    border-right: 0px none;\n    border-width: 1px 0px 0px;\n    border-style: solid none none;\n    opacity: 0.75;\n    border-color: #2A2A2A -moz-use-text-color -moz-use-text-color;\n}\n\n\n/*_________________________________________ VIDEO __________________________*/\n\nvideo::-webkit-media-controls-panel {\n    display: none !important;\n}\n\n ::-webkit-media-controls {\n    display: none !important;\n}\n\nvideo::-webkit-media-controls {\n    display: none !important;\n}\n\nvideo::-webkit-media-controls-enclosure {\n    display: none !important;\n}\n\n.custom-video-controls {\n    z-index: 2147483647;\n}\n\n.divcontrols {\n    z-index: 2147483647;\n}\n\n\n/*_________________________________ FULL SCREEN __________________________*/\n\n\n/* fullscreen */\n\nhtml:-ms-fullscreen {\n    width: 100%;\n}\n\n :-webkit-full-screen {\n    background-color: transparent;\n}\n\n\n/* hide controls on fullscreen with WebKit */\n\nfigure[data-fullscreen=true] video::-webkit-media-controls {\n    display: none !important;\n}\n\nfigure[data-fullscreen=true] {\n    max-width: 100%;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n}\n\nfigure[data-fullscreen=true] video {\n    height: 100%;\n}\n\nfigure[data-fullscreen=true] .fjcontrols-control {\n    width: 5%;\n}\n\nfigure[data-fullscreen=true] .fjcontrols-control .volumebar {\n    width: 35%;\n}\n\nfigure[data-fullscreen=true] .fjcontrols-control-text {\n    font-size: 1.4em;\n}\n\nfigure[data-fullscreen=true] span.fa.fa-3x {\n    font-size: 5em;\n}\n\nfigure[data-fullscreen=true] span.fa {\n    font-size: 2em;\n    margin-bottom: 5px;\n}\n\n\n/* Closing X */\n\n.close {\n    position: relative;\n    display: inline-block;\n    width: 50px;\n    height: 50px;\n    overflow: hidden;\n    &:hover {\n        &::before,\n        &::after {\n            background: $blue;\n        }\n    }\n    &::before,\n    &::after {\n        content: '';\n        position: absolute;\n        height: 2px;\n        width: 100%;\n        top: 50%;\n        left: 0;\n        margin-top: -1px;\n        background: #000;\n    }\n    &::before {\n        @include transform(rotate(45deg));\n    }\n    &::after {\n        @include transform(rotate(-45deg));\n    }\n    &.big {\n        @include transform(scale(3));\n    }\n    &.hairline {\n        &::before,\n        &::after {\n            height: 1px;\n        }\n    }\n    &.thick {\n        &::before,\n        &::after {\n            height: 4px;\n            margin-top: -2px;\n        }\n    }\n    &.black {\n        &::before,\n        &::after {\n            height: 8px;\n            margin-top: -4px;\n        }\n    }\n    &.heavy {\n        &::before,\n        &::after {\n            height: 12px;\n            margin-top: -6px;\n        }\n    }\n    &.pointy {\n        &::Before,\n        &::after {\n            width: 200%;\n            left: -50%;\n        }\n    }\n    &.rounded {\n        &::before,\n        &::after {\n            border-radius: 5px;\n        }\n    }\n    &.blades {\n        &::before,\n        &::after {\n            border-radius: 5px 0;\n        }\n    }\n    &.warp {\n        &::before,\n        &::after {\n            border-radius: 120% 0;\n        }\n    }\n    &.fat {\n        &::before,\n        &::after {\n            border-radius: 100%;\n        }\n    }\n}"],"sourceRoot":"webpack://"}]);

